body {
  font-family: 'Lato', sans-serif;
  overflow-x: hidden;
}

.sidebar {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.app {
  padding-bottom: 50px;
}

.main-header {
  padding: 30px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  margin: 30px 30px;
}

.main-header h1 {
  font-size: 48px;
}

.social {
  margin-top: 10px;
}

.sidebar-nav {
  list-style: none;
  padding-left: 20px;
  position: fixed;
  bottom: 0;
}

.sidebar-nav li {
  margin-bottom: 10px;
  color: var(--link-color);
  text-decoration: none;
  cursor: pointer;
}

.patreon-link {
  display: block;
  margin: 20px auto 0;
  width: 150px;
}

.patreon-link img {
  max-width: 100%;
}

@media screen and (max-height: 650px) {
  .social-wrapper {
    margin-top: 10px !important;
  }
}
@media screen and (max-height: 677px) {
  .sidebar-nav {
    margin-top: 5px;
    position: relative;
  }
  .sidebar-nav li {
    margin-bottom: 5px;
  }
  .sidebar {
    overflow-y: auto;
  }
  .patreon-link {
    margin: 5px auto 0;
  }
  .logo {
    margin: 5px 30px;
  }
}

@media all and (max-width: 800px) {
  .sidebar-nav {
    position: relative;
    display: none;
    padding: 0;
    justify-content: space-evenly;
    margin-top: 30px;
  }
}

footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  background: #65d6af;
  padding: 10px 0;
  z-index: 100;
}

footer a,
footer a:hover {
  padding: 0 3px;
  color: #fff;
  text-decoration: underline;
}

@media all and (max-width: 800px) {
  .logo {
    margin: 10px;
  }

  .logo svg {
    height: 80px;
  }

  .patreon-link {
    margin: 5px auto 10px;
    width: 130px;
  }

  .patreon-link img {
    display: block;
  }
}

@media screen and (max-width: 1280px) {
  .logo {
    margin: 10px auto;
  }
}

.card {
  --avatar-offset: 54px;
  --avatar-dim: 108px;
  display: flex;
  flex-direction: column;
  width: 280px;
  padding: 0 20px;
  border-radius: 5px;
  text-align: center;
  position: relative;
  margin-top: calc(var(--avatar-offset) + 20px);
  margin-right: 5px;
  margin-left: 5px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.5);
  display: grid;
  grid-auto-rows: 80px minmax(140px, auto) minmax(60px, auto) 80px;
}
.card .header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 17px;
}

.card .location i {
  margin-right: 5px;
}

.card .location p {
  margin: 0;
  margin-top: 2px;
}

.card .country {
  background: transparent;
  border: none;
  cursor: pointer;
  color: hsl(0, 0%, 29%);
  display: flex;
  flex-direction: row;
  align-items: center;
}

.card .country:hover {
  color: var(--tag-color);
}

.card .avatar {
  width: var(--avatar-dim);
  height: 118px;
  margin: calc(var(--avatar-offset) * -1) auto 0;
  background: transparent;
  padding: 0;
}

.avatar:hover {
  cursor: pointer;
}

.card .avatar i {
  position: absolute;
  font-size: var(--avatar-dim);
  width: var(--avatar-dim);
  height: var(--avatar-dim);
  background: hsl(0, 0%, 100%);
  color: rgb(155, 155, 155);
  border-radius: 50%;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.14),
    0 1px 10px 0 rgba(0, 0, 0, 0.12);
  display: block;
}

.card .avatar img {
  display: block;
  position: relative;
  border-radius: 50%;
  object-fit: cover;
  width: var(--avatar-dim);
  height: var(--avatar-dim);
}

.card .avatar img.broken {
  visibility: hidden;
}

.card .name {
  color: hsl(0, 0%, 29%);
  font-size: 20px;
  margin: 0 0 5px;
  word-break: break-word;
}

.card .title {
  word-break: break-word;
  color: hsl(0, 0%, 29%);
  margin: 0 0 5px;
  font-weight: 400;
}

.card .description {
  font-style: italic;
  word-break: break-word;

  color: #16385c;
  font-weight: 600;
  margin-top: 0;
  padding: 5px 2.5px;
  border-radius: 10px;
}

.card .tags {
  z-index: 2;
}

.card .tag {
  color: hsl(0, 0%, 100%);
  background: var(--button-dark-bg);
  border: 0;
  border-radius: 20px;
  display: inline-block;
  padding: 5px 10px;
  margin: 0 2.5px 5px;
  line-height: 1;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-transform: lowercase;
  font-family: monospace;
}

.card .tag:hover {
  cursor: pointer;
}

.card .channels {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: flex-end;
}

.card .channels .channel-inner {
  display: flex;
  justify-content: space-around;

  background-color: #d0f8ef;
  align-items: center;
  padding: 20px 10px 5px 10px;
  margin-right: -20px;
  margin-left: -20px;
}
.card .wave {
  position: absolute;
  bottom: 60px;
  left: 0;
  background-color: hsl(0, 0%, 100%);
  height: 30px;
  width: 100%;
  border-radius: 0% 0% 50% 50%;
}

.card .channels button {
  background: none;
  border: none;
  cursor: pointer;
}

.card .channels a,
.card .channels button {
  color: #16385c;
  text-decoration: none;
}

.card .channels a .icon,
.card .channels button .icon {
  -webkit-transition: -webkit-transform 0.3s ease;
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  -webkit-transform-origin: 50%;
          transform-origin: 50%;
}

.card .channels a:hover .icon {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.card .channels button:hover .icon {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

.card .channels button:active .icon {
  -webkit-transform: rotate(-110deg);
          transform: rotate(-110deg);
}

.card .channels a .type::first-letter {
  text-transform: uppercase;
}

.card .channels a {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card .channels .type {
  margin: 5px 0;
}

.channel-label {
  text-decoration: none;
}

.card .like-button {
  background: none;
  border: none;
  cursor: pointer;
}

.card .liked,
.card .like-button:hover i {
  color: hsl(0, 71%, 51%);
}

@media all and (max-width: 800px) {
  .card {
    margin-left: auto;
    margin-right: auto;
  }
}

@media all and (max-width: 800px) {
  main {
    flex-direction: column;
  }

  .mentors-wrapper {
    background: #fff;
    padding: 0 18px;
    position: relative;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    -webkit-transition: -webkit-transform 0.3s ease;
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  }

  .mentors-wrapper.active {
    -webkit-transform: translateY(300px);
            transform: translateY(300px);
    margin-bottom: 50px;
  }
}

@media all and (min-width: 800px) {
  .mentors-wrapper {
    flex-grow: 1;
    margin-left: 276px;
    padding-bottom: 30px;
  }

  .mentors-cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}

.mentors-cards .loader {
  font-size: 2em;
  margin-top: 15px;
}

.nothing-to-show {
  font-size: 20px;
  line-height: 1.6;
  padding: 8px;
}
#mentorCount {
  font-size: 0.65em;
  padding-left: 0.5em;
  font-weight: 400;
}

#filter {
  background-color: rgba(105, 213, 177, 0.3);
  color: #4a4a4a;
  font-size: 1.5em;
  text-transform: uppercase;
  padding: 15px 0 15px 30px;
  margin: 0;
}
@media screen and (max-height: 677px) {
  #filter {
    padding: 10px 0 10px 30px;
  }
}

.toggle-filter {
  float: right;
  margin-right: 10px;
  background: none;
  border: none;
}

.toggle-filter i {
  -webkit-transition: -webkit-transform 0.15s ease;
  transition: -webkit-transform 0.15s ease;
  transition: transform 0.15s ease;
  transition: transform 0.15s ease, -webkit-transform 0.15s ease;
}

.toggle-filter i.show-filters {
  -webkit-transform: rotateZ(90deg);
          transform: rotateZ(90deg);
}

@media all and (max-width: 800px) {
  .inputs {
    left: 0;
    opacity: 1;
    padding: 0 10px;
    position: absolute;
    width: 100%;
  }

  .inputs[aria-expanded="false"] {
    pointer-events: none;
    opacity: 0;
  }

  .inputs[aria-expanded="true"] {
    -webkit-transition: opacity 0.5s ease-in-out;
    transition: opacity 0.5s ease-in-out;
  }

  .filter-wrapper {
    order: 2;
  }
}

@media all and (min-width: 800px) {
  .toggle-filter {
    display: none;
  }

  .sidebar {
    max-width: var(--filter-width);
    position: fixed;
  }
}

@media screen and (max-width: 1280px) {
  #filter {
    padding: 5px 0 5px 30px;
  }
}

.ac .highlight {
  background: rgba(105, 213, 177, 0.3);
}

.ac .ac-menu {
  position: absolute;
  z-index: 1;
  left: var(--filter-input-offset);
  width: calc(100% - var(--filter-input-offset));
  max-height: 150px;
  overflow: auto;
  background: #fff;
  border: 1px solid rgba(155, 155, 155, 0.5);
  border-top: 0;
}

.ac .clear-btn {
  position: absolute;
  top: 50%;
  right: 10px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

@media all and (max-width: 576px) {
  .ac .ac-menu {
    width: calc(100% - var(--filter-input-offset) * 2);
  }
  .ac .clear-btn {
    right: 20px;
  }
}

.ac .ac-item {
  cursor: pointer;
  padding: 5px;
}

.clear {
  font-size: 12px;
  padding: 5px;
  border: none;
  background: none;
}

.clear:hover {
  color: var(--tag-color);
  cursor: pointer;
}

.label {
  position: absolute;
  top: -10px;
  left: 25px;
  background-color: #fff;
  text-transform: uppercase;
  color: #4a4a4a;
  font-weight: bold;
}

.input-container {
  position: relative;
  margin-top: 20px;
}

.input {
  width: 250px;
  height: 40px;
  background: none;
  border: none;
  border: 1px solid rgba(155, 155, 155, 0.5);
  border-radius: 2px;
  margin-left: var(--filter-input-offset);
  padding-left: 10px;
  color: var(--tag-color);
}

.input.input-extended {
  width: 100%;
}

@media all and (max-width: 800px) {
  .input {
    width: calc(100% - var(--filter-input-offset) * 2);
    font-size: 16px;
  }
}
.switch-container {
  position: relative;
  margin-top: 20px;
  display: flex;
  padding-left: 18px;
  justify-content: space-between;
}

@media all and (max-width: 576px) {
  .switch-container {
    padding: 0 15px;
  }
}

.switch-container > label {
  background-color: #fff;
  text-transform: uppercase;
  color: #4a4a4a;
  font-weight: bold;
}

.switch-input {
  height: 20px;
}

.switch-input input[type=checkbox] {
  height: 0;
  width: 0;
  visibility: hidden;
}

.switch-input label {
  cursor: pointer;
  text-indent: -9999px;
  width: 60px;
  height: 30px;
  background-color: grey;
  display: block;
  border-radius: 100px;
  position: relative;
  top: -25px;
  -webkit-transition: background-color 0.35s ease-in-out;
  transition: background-color 0.35s ease-in-out;
}

.switch-input label:after {
  content: '';
  position: absolute;
  top: 5px;
  left: 5px;
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 20px;
  -webkit-transition: left 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  transition: left 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
  transition: left 0.2s ease-in-out, transform 0.2s ease-in-out;
  transition: left 0.2s ease-in-out, transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}

.switch-input input:checked + label {
  background-color: var(--tag-color);
}

.switch-input input:checked + label:after {
  left: calc(100% - 5px);
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.modal-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  background: rgba(100,100,100,0.8);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: -1;
  opacity: 0;
  -webkit-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

.modal-container.active {
  opacity: 1;
  z-index: 999;
  pointer-events: auto;
}

.modal-container .modal-box {
  background: #fff;
  max-width: 780px;
  width: 95%;
  min-height: 100px;
  border-radius: 5px;
  margin: 300px auto;
  opacity: 0;
  position: relative;
  -webkit-transition: all 0.4s ease-in-out;
  transition: all 0.4s ease-in-out;
  overflow: hidden;
}

@media all and (max-width: 800px) {
  .modal-container .modal-box {
    background: #fff;
    width: 99vw;
    min-height: 100px;
    border-radius: 5px;
    margin: 10px auto;
    opacity: 0;
    -webkit-transition: all 0.4s ease-in-out;
    transition: all 0.4s ease-in-out;
  }
}

.modal-container.active .modal-box {
  margin: 100px auto;
  opacity: 1;
}

.modal-container .modal-box .close {
  border: none;
  background: #ddd;
  position: absolute;
  top: 20px;
  right: 20px;
  border-radius: 50%;
  width: 28px;
  height: 28px;
  color: #000;
  font-size: 20px;
  cursor: pointer;
}

.modal-container .modal-box .modal-head {
  border-bottom: 1px solid #e8e7e7;
}
.modal-container .modal-box .modal-head h2 {
  color: #00bc89;
  letter-spacing: .05em;
  text-transform: uppercase;
  padding: 0 25px 10px;
  margin: 0;
}
.modal-container .modal-box .modal-content {
  padding: 15px 25px 0 25px;
  color: #000022;
  overflow: hidden;
}
.scroll-helper {
  height: auto;
  max-height: calc(100vh - 200px);
  overflow-y: auto;
  margin: 0 auto 10px;
}

@media all and (max-width: 800px) {
  .modal-container .modal-box {
    padding: 5px;
    margin: 0;
  }

  .modal-container.active .modal-box {
    margin: 5px auto;
  }

  .modal-container .modal-box .modal-head h2 {
    padding: 15px;
  }

  .modal-container .modal-box .modal-content {
    padding: 0;
  }
}
